import * as Fingerprint from 'fingerprintjs'
import BaseApiService from './BaseApiService'

class Server extends BaseApiService {
    constructor() {
        super();
    }

    /**
     * @param{*}
     */
    getClassInfo = () => this.request('info');
    /**
     * @param{*}
     */
    getUserInfo = () => this.request('user');

    /**token */
    getAgoraToken = (type) => this.request('user', { type: type });
    /**
     * 
     */
    getDeviceInfo = () => this.request('room_device_info', {
        hash: new Fingerprint({ canvas: true, screen_resolution: true }).get()
    });

    /**
     * 
     */
    getTools = () => this.request('record/state/load');

    /**
     * @param{*}
     */
    getCourseState = () => this.request('record/state/load', {});

    record = (options) => this.request('record/save', options);

    // getUserInfo = (options = {}) => Vue.axios.post('user', options).then(res => res.data).catch(err => err);
    // getClassInfo = (options = {}) => Vue.axios.post('info', options).then(res => res.data).catch(err => err);
    // addCare = (options = {}) => Vue.axios.post('follow', options).then(res => res.data).catch(err => err);
    // readCare = (options = {}) => Vue.axios.post('follow_status', options).then(res => res.data).catch(err => err);

    // record = (options = {}) => Vue.axios.post('record/save', options).then(res => res.data).catch(err => err);
    // chat = (options = {}) => Vue.axios.post('chat', options).then(res => res.data).catch(err => err);
    // getChatRecord = (options = {}) => Vue.axios.post('chat_record', options).then(res => res.data).catch(err => err);
    // getEmoticon = (options = {}) => Vue.axios.post('emoticon', options).then(res => res.data).catch(err => err);

    /**
     * @param {Boolean} type  1: 2
     * @param {Number} group_user_id type1IDtype2ID
     */
    getGroupChatRecord = (type, group_user_id) => this.request('group_chat_record', { type, group_user_id });

    /**
     * @param {Boolean} type  1: 2:
     * @param {Number} group_user_id type1IDtype2ID
     * @param {String} message 
     */
    sendGroupChat = (type, group_user_id, message) => this.request('group_chat', { type, group_user_id, message });

    /**
     * @param {Boolean} type 1redirect_url,0
     */
    leaveClass = (type) => this.request('leave', { type });

    /**
     * 
     * @param {*} camera        
     * @param {*} microphone    
     */
    saveDevice(camera, microphone) {
        let options = {
            hash: new Fingerprint({ canvas: true, screen_resolution: true }).get(),
            camera: camera && camera.deviceId,
            microphone: microphone && microphone.deviceId
        }
        return this.request('room_device', options);
    }

    /**
     * 
     */
    readDevice() {
        return this.request('room_device_info', {
            hash: new Fingerprint({ canvas: true, screen_resolution: true }).get(),
        });
    }
}

export default Server;