import Api from './index'

class Record {
    constructor() {

    }

    ENTER = 1; //
    CLEAN = 2; //
    PAGE_CHANGE = 3; //
    MIC = 4; //
    DRAWING = 5; //
    ANIMATE = 6; // 
    DIAMOND = 7; //
    EXCIT = 8; //
    CHAT = 9; //
    EXIT = 12; //
    VIDEO_PUB = 13; //
    VIDEO_STOP = 14; //
    PLATFORM = 15; //
    // COURSE_CHANGE = 16; //
    POINT = 17; //
    DRAWING_TEXT = 18; //
    ANIMATE_INFO = 19; //
    CLASSOVER = 20; //
    MOVEHOUSE = 25; //
    APPRAISE = 30; //

    /**
     * 
     * @param {} type 
     * @param {} msg|JSON 
     * @param {} receive 
     */
    save(type, msg, receive) {
        receive = receive || { id: '', type: '' };
        var data = {
            receiver_id: receive.id,
            receiver_type: receive.type,
            type_id: type,
            content: msg
        };
        return new Api().record(data);
    }
}
export default new Record();