/********************************************************
 *  
 ********************************************************/
import Clock from '@/components/Clock.vue'
import event from 'events';
class ClockEvent extends event {
    //
    startTime = 0;
    //
    endTime = 0;
    constructor() {
        super();
    }

    //
    _serverTime = 0;
    //

    _clientTime = 0;
    //
    _clientStartTime = 0;
    //
    _clientEndTime = 0;
    //
    _time = 0;
    //
    _startTimeDifference = 0;
    //
    _endTimeDifference = 0;
    sid = 0;

    set serverTime(value) {
        this._clientTime = parseInt(Date.now() / 1000);
        this._serverTime = value;
    }

    get serverTime() {
        return this._serverTime;
    }

    get clientTime() {
        return this._clientTime;
    }

    get clientStartTime() {
        return this._clientStartTime;
    }
    get clientEndTime() {
        return this._clientEndTime;
    }
    get time() {
        return this._time;
    }
    get startTimeDifference() {
        return this._startTimeDifference;
    }
    get endTimeDifference() {
        return this._endTimeDifference;
    }

    start(callback) {
        this._clientStartTime = this._clientTime - (this._serverTime - this.startTime);
        this._clientEndTime = this._clientTime - (this._serverTime - this.endTime);
        this.sid = setInterval(() => {
            this._time = parseInt(Date.now() / 1000);
            this._startTimeDifference = this._time - this._clientStartTime;
            this._endTimeDifference = this._time - this._clientEndTime;
            this.emit('change', this._time);
            callback && callback(this._time);
        }, 1000);
    }

    stop() {
        clearInterval(this.sid);
    }
    /********************************************************
     *      
     *********************************************************/

    /**
     * 
     *@param value:Number 
     * */
    formatTimeValue(value) {
        var tag = "-";
        if (value >= 0) {
            tag = "";
        }
        // default format: hours:minutes:seconds
        value = Math.round(Math.abs(value));
        var hours = Math.floor(value / 3600) % 24;
        var minutes = Math.floor(value / 60) % 60;
        var seconds = value % 60;
        var result = "";

        if (hours != 0)
            result = hours + ":";

        if (result && minutes < 10)
            result += "0" + minutes + "";
        else
            result += minutes + "";

        if (seconds < 10)
            result += ":0" + seconds;
        else
            result += ":" + seconds;

        return tag + result;
    }

    formatTimeToString(time, fmt) {
        var date = new Date();
        date.setTime(time * 1000);

        fmt = fmt || "yyyy-MM-dd HH:mm:ss";
        var o = {
            "M+": date.getMonth() + 1, //
            "d+": date.getDate(), //
            "h+": date.getHours() % 12 == 0 ? 12 : date.getHours() % 12, //
            "H+": date.getHours(), //
            "m+": date.getMinutes(), //
            "s+": date.getSeconds(), //
            "q+": Math.floor((date.getMonth() + 3) / 3), //
            "S": date.getMilliseconds() //
        };
        var week = {
            "0": "/u65e5",
            "1": "/u4e00",
            "2": "/u4e8c",
            "3": "/u4e09",
            "4": "/u56db",
            "5": "/u4e94",
            "6": "/u516d"
        };
        if (/(y+)/.test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
        }
        if (/(E+)/.test(fmt)) {
            fmt = fmt.replace(RegExp.$1, ((RegExp.$1.length > 1) ? (RegExp.$1.length > 2 ? "/u661f/u671f" : "/u5468") : "") + week[date.getDay() + ""]);
        }
        for (var k in o) {
            if (new RegExp("(" + k + ")").test(fmt)) {
                fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
            }
        }
        return fmt;
    }

    toString() {
        return "ClockTime Date: " +
            "\n  : " + this.formatTimeValue(this.endTime - this.startTime) +
            "\n  : " + this.formatTimeToString(this.startTime) +
            "\n  : " + this.formatTimeToString(this.endTime) +
            "\n  : " + this.formatTimeToString(this.serverTime) +
            "\n  : " + this.formatTimeToString(this.clientTime) +
            "\n  : " + this.formatTimeToString(this.clientStartTime) +
            "\n  : " + this.formatTimeToString(this.clientEndTime) +
            "\n  : " + this.formatTimeToString(this.time) +
            "\n  : " + this.formatTimeValue(this.startTimeDifference) +
            "\n  : " + this.formatTimeValue(this.endTimeDifference);
    }
}

function getLocalTime(zone, time) {
    if (typeof zone !== 'number') return;
    var offset = new Date().getTimezoneOffset() * 60000;
    return new Date(time + offset + 60 * 60 * 1000 * zone);
}

function getLocalZone() {
    return new Date().getTimezoneOffset() / 60;
}

export {
    getLocalTime,
    getLocalZone,
    Clock,
    ClockEvent,
}